export default {
  methods: {
    async confirmUpdate(text, onCancelText) {
      try {
        await this.$confirm(text, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
      } catch (err) {
        this.$message({
          message: onCancelText
        })
        throw new Error(err)
      }
    }
  }
}